@charset "UTF-8";
.page-template-template-locations .locations-container {
  padding: 2.5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 1024px) {
  .page-template-template-locations .locations-container {
    padding: 8rem 0;
  }
}
.page-template-template-locations .locations-container .locations {
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
}
.page-template-template-locations .locations-container .locations .location {
  width: 100%;
  margin: 4rem 0;
}
.page-template-template-locations .locations-container .locations .location[open] .location__area::after {
  top: calc(50% - 25px);
  transform: rotate(180deg);
}
.page-template-template-locations .locations-container .locations .location__area {
  height: fit-content;
  padding: 2rem;
  position: relative;
  background-color: rgba(var(--primary-color), 1);
  border: 1px solid rgba(var(--primary-color), 1);
}
.page-template-template-locations .locations-container .locations .location__area .title {
  font-family: var(--lato);
  font-weight: 700;
  font-size: 3.5rem;
  line-height: normal;
  color: #ffffff;
}
.page-template-template-locations .locations-container .locations .location__area::marker {
  content: none;
}
.page-template-template-locations .locations-container .locations .location__area::after {
  content: "â–¼";
  position: absolute;
  right: 3rem;
  top: 50%;
  transform: translateY(-50%);
  color: #fff;
  font-size: 25px;
  transition: 0.3s ease-in-out;
}
.page-template-template-locations .locations-container .locations .location__content {
  max-width: 100%;
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(var(--primary-complement), 1);
  border-top: none;
}
@media (min-width: 1024px) {
  .page-template-template-locations .locations-container .locations .location__content {
    display: grid;
    grid-template-columns: 48% 48%;
    justify-content: space-between;
    align-content: center;
    align-items: start;
    justify-items: center;
  }
}
.page-template-template-locations .locations-container .locations .location__name {
  margin-bottom: 25px;
  color: rgba(var(--primary-complement), 1);
  font-family: var(--lato);
  font-weight: 400;
  font-size: 3rem;
  line-height: 3.2rem;
}
.page-template-template-locations .locations-container .locations .location__address {
  margin-bottom: 25px;
}
.page-template-template-locations .locations-container .locations .location__link {
  margin-bottom: 25px;
  font-family: var(--lato);
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.2rem;
  text-transform: uppercase;
}
.page-template-template-locations .locations-container .locations .location .contact-info {
  padding: 20px 0;
}
.page-template-template-locations .locations-container .locations .location .contact-info > * {
  margin-bottom: 10px;
}
.page-template-template-locations .locations-container .locations .location .contact-info a {
  display: inline-block;
  color: rgba(var(--secondary-color), 1);
  transition: 0.3s ease-in-out;
}
.page-template-template-locations .locations-container .locations .location .contact-info a:hover {
  color: rgba(var(--primary-color), 1);
}
.page-template-template-locations .locations-container .locations .location .contact-info .h4 {
  color: rgba(var(--primary-color), 1);
}
.page-template-template-locations .locations-container .locations .location .contact-info .phone a {
  margin-left: 10px;
}
.page-template-template-locations .locations-container .locations .location__map {
  max-width: 100%;
  width: 100%;
  max-height: 100%;
  height: fit-content;
  margin-top: 1rem;
  display: flex;
}
@media (min-width: 1024px) {
  .page-template-template-locations .locations-container .locations .location__map {
    margin-top: 5.5rem;
  }
}
.page-template-template-locations .locations-container .locations .location__map iframe {
  width: 100%;
  height: 35rem;
}
@media (min-width: 768px) {
  .page-template-template-locations .locations-container {
    display: grid;
    grid-template-columns: 65% 30%;
    justify-content: space-between;
    align-content: center;
    align-items: start;
    justify-items: center;
  }
}